.App {
  max-width:500px; margin: 0 auto; border: 1px solid #ccc;
  border-radius: 8px; box-shadow:0 4px 10px 0px rgba(0,0,0,0.5);
}
.header{
  padding: 10px 10px; background-color: #4caf50;
  color: #fff; display: flex; justify-content:space-between;
  align-items:center;
}
.header h2{
  margin: 0; font-size: 1.5rem;
}
.header button{
  border: 0px none; background-color:transparent;
  color: #fff; cursor: pointer; font-size: 1.2em;
}
.header button:hover{
  font-weight: bold; text-decoration: underline;
  color: #fc0;
}
.days{
  display: grid; grid-template-columns:repeat(7,1fr);
  background-color: #ededed;
}
.day-name{
  text-align: center; font-weight: bold; padding: 10px 0px;
  border-bottom: 1px solid #ccc; color: #555;
}
.Sat{
  color:blue;
}
.Sun{
  color:#f30;
}
.day{
  text-align: center; padding: 15px; cursor: pointer;
  transition:background-color .2s;
}
.day:hover{
  background-color: #ccc; font-weight: bold;
}
.emptyDay{
  padding: 15px;
}
.selected{
  background-color: #4caf50; color: #fff; font-weight: bold;
  border-radius: 50%;
}